@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=La+Belle+Aurore&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Open+Sans&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background: #1d1d1d;
    overflow: hidden;
}

a {
    text-decoration: none;
}

.barba-container {
    height: 100vh;
    overflow: auto;
}

h1 {
    width: max-content;
    font-size: 0px;
    color: white;
    font-family: 'Montserrat';

    span {
        font-size: 70px;
        display: inline-block;
        transition: 0.5s;
    }

    span:hover {
        animation: rubberBand 1s;
        color: #08fdd8;
    }

    .space {
        padding-right: 14px;
    }
}

h2 {
    font-size: 0px;
    color: #08fdd8;
    font-family: 'Montserrat';

    span {
        font-size: 60px;
        display: inline-block;
        transition: 0.5s;
    }

    span:hover {
        animation: rubberBand 1s;
    }

    .space {
        padding-right: 14px;
    }
}

.burger {
    visibility: hidden;
    right: 0;
    top: 50%;
    float: right;
    display: none;
    z-index: 100;

    div {
        width: 25px;
        height: 3px;
        background: #ffffff;
        margin: 4px;
    }
}

.navbar {
    background: #181818;
    float: left;
    height: 100vh;
    width: 140px;
    font-family: "Open Sans";

    .logo-container {
        background: black;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 150px;

        p {
            color: white;
            font-size: 30px;
        }
    }

    ul {
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        list-style: none;
        height: calc(100vh - 200px);
        background: transparent;
        width: 140px;

        a {
            text-decoration: none;
            font-size: 17px;
            color: #909096;
            width: 140px;
            display: flex;
            place-items: center;
            flex-direction: column;
        }

        a:hover {
            color: #b1b1bd;
        }

        div {
            width: 140px;
            height: 1px;
            background: #282828;
            margin: 15px;
        }
    }

    .socials {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        width: 140px;

        i {
            color: #84848b;
            zoom: 1.3;
        }

        i:hover {
            color: #b1b1bd;
        }
    }
}

.navbar.active {
    display: block;
    visibility: visible;
}

main {
    overflow: hidden;
}

.html-element {
    font-family: 'La Belle Aurore';
    color: #515152;
    font-size: 22px;
}

@media screen and (max-width: 900px) {
    h1,
    h2 {
        span {
            font-size: 50px;
        }
    }
}

@media screen and (max-width: 600px) {
    h1,
    h2 {
        span {
            font-size: 30px;
        }
    }

    .navbar {
        visibility: hidden;
        display: none;
    }

    .burger {
        position: relative;
        visibility: visible !important;
        display: block !important;
        right: 5%;
        top: 0;
        margin-top: 25px;
    }

    .html-element {
        font-size: 17px;
    }
}

@keyframes rubberBand {
    0% {
        transform: scale3d(1, 1, 1);
    }

    35% {
        transform: scale3d(1.25, .75, 1);
    }

    45% {
        transform: scale3d(.75, 1, 1);
    }

    60% {
        transform: scale3d(1.2, .8, 1);
    }

    75% {
        transform: scale3d(1.05, .95, 1);
    }

    100% {
        transform: scale3d(1, 1, 1);
    }
}